"use client";
import React, { useState, useEffect } from "react";

export default function signup () {
return(
    <>
     <div className="flex flex-col md:flex-row h-screen">
          {/* Left Section - Form */}
          <div className="w-full md:w-1/2 flex items-center justify-center p-8">
            <div className="w-full max-w-md">
              <h2 className="text-2xl md:text-5xl font-bold mb-6  text-blue-800">Sign Up</h2>
              <form className="space-y-4">
                <input
                  type="text"
                  placeholder="Full Name"
                  className=" text-black w-full p-3 border rounded-3xl focus:outline-none focus:ring-2 focus:ring-blue-500"
                />
                 <input
                  type="text"
                  placeholder="Username"
                  className=" text-black w-full p-3 border rounded-3xl focus:outline-none focus:ring-2 focus:ring-blue-500"
                />
                <input
                  type="email"
                  placeholder="Email"
                  className=" text-black w-full p-3 border rounded-3xl focus:outline-none focus:ring-2 focus:ring-blue-500"
                />
                <input
                  type="password"
                  placeholder="Password"
                  className=" text-black w-full p-3 border rounded-3xl focus:outline-none focus:ring-2 focus:ring-blue-500"
                />
                <button
                  type="submit"
                  className="w-full bg-blue-500 text-white p-3 rounded-3xl hover:bg-blue-600 transition"
                >
                  Login
                </button>
              </form>
              <p className="mt-4 text-center text-gray-600">
                Forgot Password?{" "}
                <a href="#" className="text-blue-500 hover:underline">
                  Click Here
                </a>
              </p>
            </div>
          </div>
    
          {/* Right Section - Image */}
          <div className="hidden md:block w-full md:w-1/2">
            <Image
              src="/images/signup.png"
              alt="Signup Image"
              width={700}
              height={1000}
              className="w-full h-full object-cover"
            />
          </div>
        </div>
    </>
)
}